<template>
    <div>
        <loading :active.sync="isLoading" v-if="isLoading"></loading>
        <div class="text-right mt-4">
            <button class="btn btn-primary " @click="openModal(true)">建立新的產品</button>
        </div>
        <table class="table mt-4 table-hover">
            <thead>
                <tr>
                    <th width="120">分類</th>
                    <th>產品名稱</th>
                    <th width="120">原價</th>
                    <th width="120">售價</th>
                    <th width="100">是否啟用</th>
                    <th width="80">編輯</th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="item in products" :key="item.id">
                    <td>{{ item.category }}</td>
                    <td>{{ item.title }}</td>
                    <td class="text-right">
                        {{ item.origin_price | currency }}
                    </td>
                    <td class="text-right">
                        {{ item.price | currency }}
                    </td>
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>未啟用</span>
                    </td>
                    <td class="btn-group" role="group">
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)">編輯</button>
                        <button class="btn btn-outline-danger btn-sm" @click="OpenDelProductModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>
        <!-- <Pagination></Pagination> -->
        <nav aria-label="Page navigation example" v-if="pagination">
            <ul class="pagination justify-content-center">
                <li class="page-item" :class="{'disabled': !pagination.has_pre}">
                    <a class="page-link" href="#" aria-label="Previous"
                    @click.prevent="getProducts(pagination.current_page-1)">
                        <span aria-hidden="true">&laquo;</span>
                        <span class="sr-only">Previous</span>
                    </a>
                </li>
                <li class="page-item" 
                v-for="page in pagination.total_pages" 
                :key="page"
                :class="{'active': pagination.current_page === page}">
                    <a class="page-link" href="#" @click.prevent="getProducts(page)">{{ page }}</a>
                </li>
                
                <li class="page-item" :class="{'disabled': !pagination.has_next}">
                <a class="page-link" href="#" aria-label="Next"
                @click.prevent="getProducts(pagination.current_page+1)">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
                </li>
            </ul>
        </nav>
        <!-- 新增/修改 Modal -->
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog" aria-labelledby="productModalLabel" aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="productModalLabel" v-if="isNew">新增產品</h5>
                    <h5 class="modal-title" id="productModalLabel" v-else>編輯產品</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        <div class="col-sm-4">
                            <div class="form-group">
                                <label for="image">輸入圖片網址</label>
                                <input type="text" class="form-control" id="image" 
                                v-model="tempProduct.image"
                                placeholder="請輸入圖片連結">
                            </div>
                            <div class="form-group">
                                <label>或 上傳圖片
                                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                                </label>
                                <input type="file" id="customFile" class="form-control" ref="files" @change="uploadFile"> 
                            </div>
                            <img :src="tempProduct.image" class="img-fluid my-3"  alt="">
                            
                        </div>
                        <div class="col-sm-8">
                            <div class="form-group">
                                <label for="title">標題</label>
                                <input type="text" class="form-control" id="title" 
                                v-model="tempProduct.title"
                                placeholder="請輸入標題">
                            </div>
                            <div class="row">
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="category">分類</label>
                                        <input type="text" class="form-control" id="category" 
                                        v-model="tempProduct.category"
                                        placeholder="請輸入分類">
                                    </div>
                                </div>
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="unit">單位</label>
                                        <input type="text" class="form-control" id="unit" 
                                        v-model="tempProduct.unit"
                                        placeholder="請輸入單位">
                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="origin_price">原價</label>
                                        <input type="number" class="form-control" id="origin_price" 
                                        v-model.number="tempProduct.origin_price"
                                        placeholder="請輸入原價">
                                    </div>
                                </div>
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="price">售價</label>
                                        <input type="number" class="form-control" id="price" 
                                        v-model.number="tempProduct.price"
                                        placeholder="請輸入售價">
                                    </div>
                                </div>
                            </div>

                            <hr>

                            
                            <div class="form-group">
                                <label for="description">產品描述</label>
                                <textarea type="text" class="form-control" id="description" 
                                v-model="tempProduct.description"
                                placeholder="請輸入產品描述"></textarea>
                            </div>
                            <div class="form-group">
                                <label for="content">說明內容</label>
                                <textarea type="text" class="form-control" id="content" 
                                v-model="tempProduct.content"
                                placeholder="請輸入說明內容"></textarea>
                            </div>

                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" value="" id="is_enabled"
                                v-model="tempProduct.is_enabled"
                                :true-value="1"
                                :false-value="0"
                                >
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>

                            
                            
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="updateProduct">確認</button>
                </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除產品</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempProduct.title }}</strong> 商品(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger"
                    @click="delProduct"
                    >確認刪除</button>
                </div>
                </div>
            </div>
        </div>
    </div>
    
</template>


<script>
// import Pagination from '../Pagination';
import $ from 'jquery';
export default {
    components: {
        // Pagination,
    },
    data(){
        return{
            products: [],
            tempProduct: {},
            pagination: {},
            isNew: false,
            isLoading: false,
            status: {
                fileUploading: false
            }
        }
    },
    methods: {
        getProducts(page = 1){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/products?page=${page}`;
            console.log(api);
            const vm = this;
            
            vm.isLoading = true;
            vm.$http.get(api).then((res) => {
                vm.pagination = res.data.pagination;
                console.log(res);
                vm.products = res.data.products;
                
                vm.isLoading = false;
            });

        },
        openModal(isNew, item){
            
            if(isNew){
                this.tempProduct = {};
                this.isNew = true;
            }
            else{
                this.tempProduct = Object.assign({}, item);
                this.isNew = false;
            }
            $('#productModal').modal('show');
        },
        updateProduct(){
            let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product`;
            let httpMethod = 'post';
            const vm = this;
            if(!vm.isNew){
                api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product/${vm.tempProduct.id}`;
                httpMethod = 'put';
            }
            vm.$http[httpMethod](api, { data: vm.tempProduct }).then((res) => {
                console.log(res);
                
                // vm.products = res.data.products;
                if(res.data.success){
                    $('#productModal').modal('hide');
                    vm.getProducts();

                }
                else{
                    $('#productModal').modal('hide');
                    vm.getProducts();
                    console.log('新增失敗');
                }
            });
        },
        OpenDelProductModal(item){
            // console.log('hello');
            this.tempProduct = Object.assign({}, item);
            $('#delProductModal').modal('show');
        },
        delProduct(){
            const vm = this;
            let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/product/${vm.tempProduct.id}`;
            
            
            vm.$http.delete(api).then((res) => {
                console.log(res);
                
                // vm.products = res.data.products;
                if(res.data.success){
                    $('#delProductModal').modal('hide');
                    vm.getProducts();

                }
                else{
                    $('#delProductModal').modal('hide');
                    vm.getProducts();
                    console.log('新增失敗');
                }
            });
        },
        uploadFile(){
            
            // console.log(this);
            const uploadedFile = this.$refs.files.files[0];
            // console.log(uploadedFile);
            const vm = this;
            vm.status.fileUploading = true;
            const formData = new FormData();
            formData.append('file-to-upload', uploadedFile);
            const url = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/upload`;
            
            this.$http.post(url, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            }).then((response) => {
                console.log(response.data);
                
                if(response.data.success){
                    vm.$set(vm.tempProduct, 'image', response.data.imageUrl);
                    vm.status.fileUploading = false;
                }
                else{
                    vm.status.fileUploading = false;
                    this.$bus.$emit('messsage:push', response.data.message, 'danger');
                
                }
                

            })
        }
    },
    created() {
        this.getProducts();
        // this.$bus.$emit('messsage:push', 'message~~', 'danger');
    }
}
</script>



