<template>
    <div>
        <loading :active.sync="isLoading" v-if="isLoading"></loading>
        <div class="row mt-4">
            <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
                <div class="card border-0 shadow-sm">
                    <div style="height: 150px; background-size: cover; background-position: center"
                    :style="{backgroundImage: `url(${item.image})`}"
                    >
                    </div>
                    <div class="card-body">
                    <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
                    <h5 class="card-title">
                        <a href="#" class="text-dark">{{ item.title }}</a>
                    </h5>
                    <p class="card-text">{{ item.description }}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="!item.price">原價 {{ item.origin_price | currency }} 元</div>
                        <del class="h6" v-if="item.price">原價 {{ item.origin_price | currency }} 元</del>
                        <div class="h5" v-if="item.price">現在只要 {{ item.price | currency }} 元</div>
                    </div>
                    </div>
                    <div class="card-footer d-flex">
                    <button type="button" class="btn btn-outline-secondary btn-sm"
                    @click="getProduct(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        查看更多
                    </button>
                    <button type="button" class="btn btn-outline-danger btn-sm ml-auto"
                    @click="addToCart(item.id)">
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
                        加到購物車
                    </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>{{ product.title }}</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <img class="img-fluid" :src="product.image" alt="">
                    <p class="my-4">{{ product.content }}</p>
                    <div class="d-flex justify-content-between align-items-baseline">
                        <div class="h5" v-if="!product.price">原價 {{ product.origin_price | currency }} 元</div>
                        <del class="h6" v-if="product.price">原價 {{ product.origin_price | currency }} 元</del>
                        <div class="h5" v-if="product.price">現在只要 {{ product.price | currency }} 元</div>
                    </div>
                    <select name="" class="form-control mt-3" id="" v-model="product.num">
                        
                        <option :value="num" v-for="num in 10" :key="num">
                            選購 {{ num }} {{ product.unit }}
                        </option>
                    </select>
                </div>
                <div class="modal-footer">
                    <div class="text-muted text-nowrap mr-3">
                        小計 <strong>{{ product.num * product.price }}</strong> 元
                    </div>
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary"
                    @click="addToCart(product.id, product.num)"
                    >
                        <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === product.id"></i>
                        加到購物車
                    </button>
                </div>
                </div>
            </div>
        </div>
        <div class="container my-5" style="width: 600px">
            <table class="table mt-4 table-hover">
                <thead>
                    <tr>
                        <th width="120"></th>
                        <th>品名</th>
                        <th width="120">數量</th>
                        <th width="120">單價</th>
                        

                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in carts.carts" :key="item.id">
                        <td>
                            <button class="btn btn-outline-danger btn-sm" 
                            @click="delCart(item.id)"
                            >
                                <i class="far fa-trash-alt"></i>
                            </button>
                        </td>
                        <td class="align-middle">
                            {{ item.product.title }}
                            <div class="text-success" v-if="item.coupon">
                                已套用優惠券
                            </div>
                        </td>
                        
                        <td>
                            {{ item.qty }}/{{ item.product.unit }}
                        </td>
                        <td class="text-right">
                            {{ item.product.price | currency }}
                        </td>
                    </tr>
                    <tr class="lead">
                        <td></td>
                        <td></td>
                        <td>總計</td>
                        <td class="text-right">{{ carts.total | currency }}</td>
                    </tr>
                    <tr class="text-success lead" v-if="carts.total !== carts.final_total">
                        <td></td>
                        <td></td>
                        <td>折扣價</td>
                        <td class="text-right">{{ carts.final_total | currency }}</td>
                    </tr>
                </tbody>
            </table>
            <div class="input-group mb-3 input-group-sm">
                <input type="text" class="form-control" 
                placeholder="請輸入優惠碼"
                v-model="coupon_code">
                <div class="input-group-append">
                    <button class="btn btn-outline-secondary"
                    @click="addCouponCode">
                        套用優惠碼
                    </button>
                </div>
            </div>
        </div>

        <div class="my-5 row justify-content-center">
            <form class="col-md-6" @submit.prevent="createOrder">
                <div class="form-group">
                    <label for="useremail">Email</label>
                    <input type="email" class="form-control" name="email" id="useremail"
                        v-model="form.user.email" 
                        placeholder="請輸入 Email"
                        :class="{'is-invalid': errors.has('email')}"
                        v-validate="'required|email'"
                        >
                    <span class="text-danger" v-if="errors.has('email')">
                        {{ errors.first('email') }}
                    </span>
                </div>
            
                <div class="form-group">
                    <label for="username">收件人姓名</label>
                    <input type="text" class="form-control" name="name" id="username"
                        v-model="form.user.name"
                        :class="{'is-invalid': errors.has('name')}"
                        v-validate="'required'"
                        placeholder="輸入姓名">
                    <span class="text-danger" v-if="errors.has('name')">姓名不得為空</span>
                </div>
            
                <div class="form-group">
                    <label for="usertel">收件人電話</label>
                    <input type="tel" class="form-control" id="usertel" name="usertel" 
                    v-model="form.user.tel" 
                    placeholder="請輸入電話"
                    :class="{'is-invalid': errors.has('usertel')}"
                    v-validate="'required'">
                    <span class="text-danger" v-if="errors.has('usertel')">電話不得為空</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">收件人地址</label>
                    <input type="address" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                        placeholder="請輸入地址"
                        :class="{'is-invalid': errors.has('address')}"
                    v-validate="'required'">
                    <span class="text-danger" v-if="errors.has('address')">地址欄位不得留空</span>
                </div>
            
                <div class="form-group">
                    <label for="useraddress">留言</label>
                    <textarea name="" id="" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                    </div>
                    <div class="text-right">
                    <button class="btn btn-danger">送出訂單</button>
                </div>
            </form>
        </div>
        
        
    </div>
</template>

<script>
import $ from 'jquery';

export default {
    data(){
        return{
            products: [],
            product: {
            },
            pagination: {},
            isLoading: false,
            status: {
                loadingItem: ''
            },
            carts: [],
            coupon_code: '',
            form: {
                user: {
                    name: '',
                    email: '',
                    tel: '',
                    address: ''
                },
                message: ''

            }
            

        }
    },
    methods: {
        getProducts(page = 1){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/products?page=${page}`;
            console.log(api);
            const vm = this;
            
            vm.isLoading = true;
            vm.$http.get(api).then((res) => {
                console.log(res);
                vm.products = res.data.products;
                vm.pagination = res.data.pagination;
                vm.isLoading = false;
            });

        },
        getProduct(id){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/product/${id}`;
            const vm = this;
            vm.status.loadingItem = id;
            vm.$http.get(api).then((res) => {
                console.log(res);
                vm.product = res.data.product;
                $('#productModal').modal('show');
                vm.status.loadingItem = '';
                vm.product.num = 1;
                
            });
            
            
        },
        addToCart(id, qty = 1){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart`;
            const vm = this;
            vm.status.loadingItem = id;
            const cart = {
                product_id: id,
                qty
            }
            vm.$http.post(api, { data: cart }).then((res) => {
                console.log(res);
                
                vm.status.loadingItem = '';
                $('#productModal').modal('hide');
                vm.getCart();
            });
        },
        getCart(){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart`;
            const vm = this;

            vm.$http.get(api).then((res) => {
                console.log(res);
                vm.carts = res.data.data;
                // vm.cartFinalTotal = res.data.data.final_total;
            });
        },
        delCart(id){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/cart/${id}`;
            const vm = this;
            vm.isLoading = true;

            vm.$http.delete(api).then((res) => {
                console.log(res);
                vm.getCart();
                vm.isLoading = false;
                
            });
        },
        addCouponCode(){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/coupon`;
            const vm = this;
            vm.isLoading = true;
            const coupon = {
                code: vm.coupon_code,
            };
            vm.$http.post(api, { data: coupon }).then((res) => {
                console.log(res);
                vm.getCart();
                vm.isLoading = false;
                
            });
        },
        createOrder(){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/order`;
            const vm = this;
            const form = vm.form;
            vm.isLoading = true;
            this.$validator.validate().then((result) => {
                if (result) {
                    vm.$http.post(api, { data: form }).then((res) => {
                        console.log('訂單已建立' ,res);
                        vm.isLoading = false;
                        if(res.data.success){
                            vm.$router.push(`/customer_checkout/${res.data.orderId}`);
                        }
                        
                    });
                }
                else{
                    console.log('驗證失敗');
                    vm.isLoading = false;
                }
            });
            
        }
    },
    created(){
        this.getProducts();
        this.getCart();
    }
}
</script>




