<template>
    <div>
        
        <loading :active.sync="isLoading" v-if="isLoading"></loading>
        
        <table class="table mt-4 table-hover">
            <thead>
                <tr>
                    <th width="120">購買時間</th>
                    <th width="120">Email</th>
                    <th width="120">購買款項</th>
                    <th width="120">應付金額</th>
                    <th width="100">是否付款</th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="item in orders" :key="item.id">
                    <td>{{  item.create_at | moment("YYYY-MM-DD HH:mm:ss") }}</td>
                    <td>{{ item.user.email }}</td>
                    <td>
                        <ul class="list-unstyled">
                            <li v-for="product in item.products" :key="product.id">
                                {{ product.product.title }} * {{ product.qty }}/{{ product.product.unit }}
                            </li>
                        </ul>
                    </td>
                    <td class="text-right">
                        {{ item.total | currency }}
                    </td>
                    <td>
                        <span v-if="item.is_paid" class="text-success">已付款</span>
                        <span v-else>未付款</span>
                    </td>
                   
                </tr>
            </tbody>
        </table>
        <!-- <Pagination></Pagination> -->
        <nav aria-label="Page navigation example" v-if="pagination">
            <ul class="pagination justify-content-center">
                <li class="page-item" :class="{'disabled': !pagination.has_pre}">
                <a class="page-link" href="#" aria-label="Previous"
                @click.prevent="getOrders(pagination.current_page-1)">
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                </a>
                </li>
                <li class="page-item" 
                v-for="page in pagination.total_pages" 
                :key="page"
                :class="{'active': pagination.current_page === page}">
                    <a class="page-link" href="#" @click.prevent="getOrders(page)">{{ page }}</a>
                </li>
                
                <li class="page-item" :class="{'disabled': !pagination.has_next}">
                <a class="page-link" href="#" aria-label="Next"
                @click.prevent="getOrders(pagination.current_page+1)">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
                </li>
            </ul>
        </nav>
    </div>
    
</template>


<script>

import $ from 'jquery';
export default {

    data(){
        return{
            orders: [],
            tempProduct: {},
            pagination: {},
            isNew: false,
            isLoading: false,
            status: {
                fileUploading: false
            }
        }
    },
    methods: {
        getOrders(page = 1){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/orders?page=${page}`;
            console.log(api);
            const vm = this;
            
            vm.isLoading = true;
            vm.$http.get(api).then((res) => {
                console.log(res);
                vm.orders = res.data.orders;
                vm.pagination = res.data.pagination;
                vm.isLoading = false;
            });

        },
        
        
        
    },
    created() {
        this.getOrders();
        // this.$bus.$emit('messsage:push', 'message~~', 'danger');
    }
}
</script>



