<template>
    <div>
        
        <loading :active.sync="isLoading" v-if="isLoading"></loading>
        <div class="text-right mt-4">
            <button class="btn btn-primary " @click="openModal(true)">建立優惠券</button>
        </div>
        <table class="table mt-4">
            <thead>
                <tr>
                    <th>優惠券</th>
                    <th>優惠碼</th>
                    <th width="120">折扣</th>
                    <th width="120">到期日</th>
                    <th width="100">是否啟用</th>
                    <th width="80">編輯</th>

                </tr>
            </thead>
            <tbody>
                <tr v-for="item in coupons" :key="item.id">
                    <td>{{ item.title }}</td>
                    <td>{{ item.code }}</td>
                    <td>{{ item.percent }}</td>
                    
                    <td>{{ item.due_date }}</td>
                    
                    <td>
                        <span v-if="item.is_enabled" class="text-success">啟用</span>
                        <span v-else>未啟用</span>
                    </td>
                    <td class="btn-group" role="group">
                        <button class="btn btn-outline-primary btn-sm" @click="openModal(false, item)">編輯</button>
                        <button class="btn btn-outline-danger btn-sm" @click="OpenDelCouponModal(item)">刪除</button>
                    </td>
                </tr>
            </tbody>
        </table>
        <!-- <Pagination></Pagination> -->
        <nav aria-label="Page navigation example" v-if="pagination">
            <ul class="pagination justify-content-center">
                <li class="page-item" :class="{'disabled': !pagination.has_pre}">
                <a class="page-link" href="#" aria-label="Previous"
                @click.prevent="getCounpons(pagination.current_page-1)">
                    <span aria-hidden="true">&laquo;</span>
                    <span class="sr-only">Previous</span>
                </a>
                </li>
                <li class="page-item" 
                v-for="page in pagination.total_pages" 
                :key="page"
                :class="{'active': pagination.current_page === page}">
                    <a class="page-link" href="#" @click.prevent="getCounpons(page)">{{ page }}</a>
                </li>
                
                <li class="page-item" :class="{'disabled': !pagination.has_next}">
                <a class="page-link" href="#" aria-label="Next"
                @click.prevent="getCounpons(pagination.current_page+1)">
                    <span aria-hidden="true">&raquo;</span>
                    <span class="sr-only">Next</span>
                </a>
                </li>
            </ul>
        </nav>
        <!-- 新增/修改 Modal -->
        <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" aria-labelledby="couponModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="couponModalLabel" v-if="isNew">建立優惠券</h5>
                    <h5 class="modal-title" id="couponModalLabel" v-else>編輯優惠券</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div class="row">
                        
                        <div class="col-sm-12">
                            <div class="form-group">
                                <label for="title">標題</label>
                                <input type="text" class="form-control" id="title" 
                                v-model="tempCounpon.title"
                                placeholder="請輸入標題">
                            </div>
                            <div class="form-group">
                                <label for="code">優惠碼</label>
                                <input type="text" class="form-control" id="code" 
                                v-model="tempCounpon.code"
                                placeholder="請輸入優惠碼">
                            </div>
                            <div class="row">
                                
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="percent">折扣</label>
                                        <input type="number" class="form-control" id="percent" 
                                        v-model.number="tempCounpon.percent"
                                        placeholder="請輸入折扣">
                                    </div>
                                </div>
                                <div class="col-sm-6">
                                    <div class="form-group">
                                        <label for="due_date">到期日</label>
                                        <input type="date" class="form-control" id="due_date" 
                                        v-model="tempCounpon.due_date"
                                        placeholder="請輸入到期日">
                                    </div>
                                </div>
                            </div>
                                

                            <div class="form-check">
                                <input class="form-check-input" type="checkbox" value="" id="is_enabled"
                                v-model="tempCounpon.is_enabled"
                                :true-value="1"
                                :false-value="0"
                                >
                                <label class="form-check-label" for="is_enabled">
                                是否啟用
                                </label>
                            </div>

                            
                            
                        </div>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-primary" @click="updateCoupon">確認</button>
                </div>
                </div>
            </div>
        </div>
        <div class="modal fade" id="delCounponModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
            <div class="modal-dialog" role="document">
                <div class="modal-content border-0">
                <div class="modal-header bg-danger text-white">
                    <h5 class="modal-title" id="exampleModalLabel">
                    <span>刪除優惠券</span>
                    </h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    是否刪除 <strong class="text-danger">{{ tempCounpon.title }}</strong> 優惠券(刪除後將無法恢復)。
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
                    <button type="button" class="btn btn-danger"
                    @click="delCoupon"
                    >確認刪除</button>
                </div>
                </div>
            </div>
            </div>
    </div>
    
</template>


<script>
import Pagination from '../Pagination';
import $ from 'jquery';
export default {
    components: {
        Pagination,
    },
    data(){
        return{
            coupons: [],
            tempCounpon: {},
            pagination: {},
            isNew: false,
            isLoading: false,
            status: {
                fileUploading: false
            }
        }
    },
    methods: {
        getCounpons(page = 1){
            const api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/coupons?page=${page}`;
            console.log(api);
            const vm = this;
            
            vm.isLoading = true;
            vm.$http.get(api).then((res) => {
                console.log(res);
                vm.coupons = res.data.coupons;
                vm.pagination = res.data.pagination;
                vm.isLoading = false;
            });

        },
        openModal(isNew, item){
            
            if(isNew){
                this.tempCounpon = {};
                this.isNew = true;
            }
            else{
                this.tempCounpon = Object.assign({}, item);
                this.isNew = false;
            }
            $('#couponModal').modal('show');
        },
        updateCoupon(){
            let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/coupon`;
            let httpMethod = 'post';
            const vm = this;
            if(!vm.isNew){
                api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/coupon/${vm.tempCounpon.id}`;
                httpMethod = 'put';
            }
            vm.$http[httpMethod](api, { data: vm.tempCounpon }).then((res) => {
                console.log(res);
                
                // vm.products = res.data.products;
                if(res.data.success){
                    $('#couponModal').modal('hide');
                    vm.getCounpons();

                }
                else{
                    $('#couponModal').modal('hide');
                    vm.getCounpons();
                    console.log('新增失敗');
                }
            });
        },
        OpenDelCouponModal(item){
            // console.log('hello');
            this.tempCounpon = Object.assign({}, item);
            $('#delCounponModal').modal('show');
        },
        delCoupon(){
            const vm = this;
            let api = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/coupon/${vm.tempCounpon.id}`;
            
            
            vm.$http.delete(api).then((res) => {
                console.log(res);
                
                // vm.products = res.data.products;
                if(res.data.success){
                    $('#delCounponModal').modal('hide');
                    vm.getCounpons();

                }
                else{
                    $('#delCounponModal').modal('hide');
                    vm.getCounpons();
                    console.log('新增失敗');
                }
            });
        },
        uploadFile(){
            
            // console.log(this);
            const uploadedFile = this.$refs.files.files[0];
            // console.log(uploadedFile);
            const vm = this;
            vm.status.fileUploading = true;
            const formData = new FormData();
            formData.append('file-to-upload', uploadedFile);
            const url = `${ process.env.APIPATH }/api/${ process.env.CUSTOMPATH }/admin/upload`;
            
            this.$http.post(url, formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                }
            }).then((response) => {
                console.log(response.data);
                
                if(response.data.success){
                    vm.$set(vm.tempCounpon, 'image', response.data.imageUrl);
                    vm.status.fileUploading = false;
                }
                else{
                    vm.status.fileUploading = false;
                    this.$bus.$emit('messsage:push', response.data.message, 'danger');
                
                }
                

            })
        }
    },
    created() {
        this.getCounpons();
        // this.$bus.$emit('messsage:push', 'message~~', 'danger');
    }
}
</script>



